.root {
  display: block;
  width: 21px;
  height: 21px;
}

.original {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.custom {
  cursor: pointer;
  user-select: none;
  position: absolute;
  width: 21px;
  height: 21px;
  background-color: white;
  border-radius: 50%;
  border: 1px solid rgba(158, 158, 158, 1);
  &::before {
    content: "";
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
    width: 12px;
    height: 12px;
    background-color: var(--radiobutton-checked-bg-color);
    border-radius: 50%;
    transition-duration: 0.2s;
  }
}

.original:checked + .custom::before {
  opacity: 1;
}
